/* eslint-disable import/no-extraneous-dependencies */
/** @jsx jsx */
import * as React from 'react';
import { waitForElement, waitFor, wait, render } from '@testing-library/react';
import Mo from '../serializer';
import { ImageData, Image as ImageMoPlugin } from '@ali/4ever-mo';
import { ImagePlugin } from '@ali/4ever-bamboo';
import { Inline, Value } from '@ali/4ever-cangjie';
import Editor, { imageConfig, imagePreviewMockFn, imageDownloadMockFn } from '../editor';
import { renderEditor, fireEvent, jsx, getHotkeyEvent, HOTKEYS, waitForTime } from '@ali/4ever-dev-test';
import { getContent } from '../helper';
import fitRotate from '../../plugins/image/utils/fitRotate';
import filterImageData from '../../plugins/image/utils/filterImageData';
import { dropImages, filterTransferableImages } from '../../plugins/image/utils/filterImages';
import { getResizeCursor } from '../../plugins/image/utils';
import { innerSrc, outerSrc } from './utils';
import { ToolbarLayout } from '@ali/we-design-next';

let origin_value
beforeAll(() => {
  origin_value = process.env.RUNTIME_ENV;
  process.env.RUNTIME_ENV = ToolbarLayout.UITEST_FLAG
})

afterAll(() => {
  process.env.RUNTIME_ENV = origin_value;
})

Object.defineProperty(imageConfig, 'previewImage', {
  get() {
    imagePreviewMockFn();
    return imagePreviewMockFn;
  },
});

export function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

const IMAGE_URL = 'https://cn.bing.com/az/hprichbg/rb/TeslaCoil_EN-CN1604235004_1920x1080.jpg';

const NESW = 'nesw-resize';
const NWSE = 'nwse-resize';
const NS = 'ns-resize';
const EW = 'ew-resize';

const _Image = global.Image;
const _URL = global.URL;

const value = Mo.jsonMLToValue([
  'root', {},
  ['p',
    ['img', {
      src: IMAGE_URL,
      width: 1920,
      height: 1080,
      opacity: 0.5,
      outline: {
        width: 1,
        type: 'solid',
        color: '#002060',
      },
      type: 'jpg',
    }],
  ],
]);

const complexValue = Mo.jsonMLToValue([
  'root', {},
  ['p',
    ['img', {
      src: IMAGE_URL,
      width: 1000,
      height: 800,
      opacity: 0.5,
      outline: {
        width: 6,
        type: 'solid',
        color: '#002060',
      },
      rectClip: {
        left: 900,
        right: 20,
        top: 200,
        bottom: 80,
      },
      type: 'jpg',
    }],
  ],
]);

const fitRotateTest = [{
  before: 89,
  after: 90,
}, {
  before: 91,
  after: 90,
}, {
  before: 60,
  after: 60,
}];

const resizeCursorTest = [{
  before: -22.5,
  after: NS,
}, {
  before: 22.5,
  after: NESW,
}, {
  before: 77.5,
  after: EW,
}, {
  before: 122.5,
  after: NWSE,
}];

const filterDataTest: Array<{before: ImageData; after: ImageData; list: ImagePlugin.FilterDataItem[]; }> = [{
  before: {
    src: 'xxx',
    width: 300,
    height: 100,
    rectClip: {
      left: 20,
      right: 30,
      top: 40,
    },
    outline: {
      width: 2,
      type: 'solid',
      color: 'red',
    },
    radius: 8,
    shadow: 'xxx',
    rotation: 30,
  }, after: {
    src: 'xxx',
    width: 350,
    height: 140,
    radius: 8,
  },
  list: ['rectClip', 'rotation', 'outline', 'shadow'],
}, {
  before: {
    src: 'xxx',
    width: 300,
    height: 100,
    rectClip: {
      left: 20,
      right: 30,
      top: 40,
    },
    outline: {
      width: 2,
      type: 'solid',
      color: 'red',
    },
    shadow: 'xxx',
  }, after: {
    src: 'xxx',
    width: 300,
    height: 100,
    rectClip: {
      left: 20,
      right: 30,
      top: 40,
    },
  },
  list: ['shadow', 'outline'],
}];

describe('Bi/image/ui', () => {
  afterAll(() => {
    global.Image = _Image;
  });
  it('转存图片', async () => {
    const fn = jest.fn();
    global.Image = class {
      naturalWidth;
      naturalHeight;
      onload;
      constructor(src?: string) {
        if (src) {
          this.src = src;
        }
      }
      set src(url: string) {
        this?.onload();
        this.naturalWidth = 384;
        this.naturalHeight = 108;
        fn();
      }
    };
    const { editor, content } = await renderEditor(
      <Editor
        defaultValue={Mo.textToValue('')}
        singleLayout={[{ items: [{ name: 'image' }] }]}
      />,
    );
    fireEvent.paste(editor, 'text/html', `
      <div>
        <img src="${outerSrc}" />
      </div>
    `);

    fireEvent.keyDown(editor, getHotkeyEvent('ctrl+v'));
    await waitForElement(() => content.querySelector('img[data-type=image]'));
    const img = content.querySelector('[data-type=image]')
    expect(!!img).toBe(true);
    expect(img.getAttribute('data-src')).toBe(innerSrc);
    expect(fn).toHaveBeenCalledTimes(1);
  });
  it('更新图片宽高', async () => {
    const fn = jest.fn();
    global.Image = class {
      naturalWidth;
      naturalHeight;
      onload;
      constructor(src?: string) {
        if (src) {
          this.src = src;
        }
      }
      set src(url: string) {
        this?.onload();
        this.naturalWidth = 384;
        this.naturalHeight = 108;
        fn();
      }
    };
    const { editor, content, container } = await renderEditor(
      <Editor
        defaultValue={Mo.textToValue('')}
        singleLayout={[{ items: [{ name: 'image' }] }]}
      />,
    );
    fireEvent.paste(editor, 'text/html', `
      <div>
        <img src="${innerSrc}" />
      </div>
    `);

    fireEvent.keyDown(editor, getHotkeyEvent('ctrl+v'));
    await sleep(100);
    await waitForElement(() => content.querySelector('img[data-type=image]'));
    const img = content.querySelector('[data-type=image]');
    expect(!!img).toBe(true);
    expect(img.getAttribute('data-src')).toBe(innerSrc);
    expect(fn).toHaveBeenCalledTimes(1);
  });

  it('image 右侧面板中设置边框样式和大小后，边框面板不会消失', async() => {
    const { content, findByTestId, queryByTestId, findAllByTestId } = await renderEditor(<Editor defaultValue={value} />);
    const img = content.querySelector('[data-type="image"]');
    fireEvent.contextMenu(img);

    await findByTestId('bi-contextmenu');

    const optionMenuItem = await findByTestId('menu-item-image-options');

    fireEvent.click(optionMenuItem);
    const sidebar = await findByTestId('image-sidebar');

    expect(sidebar).toBeInstanceOf(Element);

    const borderBtn = await findByTestId('image-sidebar-border-button');
    fireEvent.click(borderBtn);
    await queryByTestId('Popup-image-sidebar-border-dropdown');
    const lineOptions = await findAllByTestId('SelectWrap-CommonLineSelect');
    fireEvent.click(lineOptions[0]);
    const size6 = await findByTestId('option-6');
    fireEvent.click(size6);
    await wait();
    fireEvent.click(lineOptions[1]);
    const styleDash = await findByTestId('option-dashed');
    fireEvent.click(styleDash);

    const imageBorder = await findByTestId('editor-image-real-box');
    const imageBorderStyle = getComputedStyle(imageBorder);
    expect(imageBorderStyle.borderStyle).toBe('dashed');
    expect(imageBorderStyle.borderWidth.toString()).toBe('6px');
  });

  it.skip('image 有渲染出透明度以及轮廓', async () => {
    const { container } = render(<Editor defaultValue={value} />);
    expect(getContent(container).find('MetaImgFrame').prop('style').opacity).toBe(0.5);
    expect(getContent(container).find('MetaImgFrame').prop('style').border).toEqual(expect.stringContaining('solid'));
  });
  it('图片旋转在断点处自动吸附', () => {
    fitRotateTest.forEach(({ before, after }) => {
      expect(JSON.stringify(fitRotate(before))).toBe(JSON.stringify(after));
    });
  });
  it('图片resize光标', () => {
    resizeCursorTest.forEach(({ before, after }) => {
      expect(getResizeCursor(0, before)).toBe(after);
      expect(getResizeCursor(0, before - 180)).toBe(after);
      expect(getResizeCursor(0, before + 180)).toBe(after);
    });
  });
  it('粘贴时过滤图片数据', () => {
    filterDataTest.forEach(({ before, after, list }) => {
      expect(JSON.stringify(filterImageData(before, list))).toBe(JSON.stringify(after));
    });
  });
  it('粘贴时获取需要转换的图片', () => {
    const value = Mo.jsonMLToValue([
      'root', {},
      ['p',
        ['img', {
          src: 'aaa'
        }],
        ['img', {
          src: 'bbb'
        }],
        ['img', {
          src: 'ccc'
        }],
      ],
    ]);
    const transferableImages = filterTransferableImages(value.document, (url) => url === 'aaa');
    const transferableSrc = transferableImages.map(image => image.data.src).toString();
    expect(transferableSrc).toBe('aaa');
    const droppedFragment = dropImages(value.document, (url) => url === 'aaa');
    const images = droppedFragment.filterDescendants((node) => ImageMoPlugin.isImage(node));
    const dropRes = images.map((image) => (image as Inline).data.src).toString();
    const expectRes = 'bbb,ccc';
    expect(dropRes).toBe(expectRes);
  });
  // TODO: FIXME @淼淼
  // imagePreviewMockFn 读一次就会执行一次，之前由于每次点击后都会 re-render 一次，导致有个读操作，所以测试会过
  it.skip('图片预览', async () => {
    const { content, findByTestId } = await renderEditor(<Editor defaultValue={value} />);
    let img = content.querySelector('[data-type="image"]');
    fireEvent.click(img);
    // 单击图片后，图片被选中
    const imageSelect = await findByTestId('image-select');

    img = imageSelect.querySelector('img');
    const imagePreviewCalls = imagePreviewMockFn.mock.calls.length;
    fireEvent.click(img);
    await waitFor(() => expect(imagePreviewMockFn.mock.calls.length).toBe(imagePreviewCalls + 1));
  });

  it('图片面板 - 还原', async () => {
    const fn = jest.fn();
    global.Image = class {
      naturalWidth;
      naturalHeight;
      onload;
      constructor(src?: string) {
        if (src) {
          this.src = src;
        }
      }
      set src(url: string) {
        this?.onload();
        this.naturalWidth = 384;
        this.naturalHeight = 108;
        fn();
      }
    };
    const configValue = Mo.jsonMLToValue([
      'root', {},
      ['p',
        ['img', {
          src: 'https://cn.bing.com/az/hprichbg/rb/TeslaCoil_EN-CN1604235004_1920x1080.jpg',
          width: 192,
          height: 108,
          outline: {
            width: 1,
            type: 'solid',
            color: '#002060',
          },
          shadow: '1px 1px 10px 0 rgba(0, 0, 0, 0.12)',
          radius: 4,
          type: 'jpg',
        }],
      ],
    ]);
    const { content, findByTestId } = await renderEditor(<Editor defaultValue={configValue} />);
    const img = content.querySelector('[data-type="image"]');
    let imgBorder = await findByTestId('editor-image-real-box');

    expect(imgBorder?.style?.border).toBe('1px solid #002060');
    expect(imgBorder?.style?.borderRadius).toBe('4px');
    expect(imgBorder?.style?.boxShadow === '1px 1px 10px 0 rgba(0, 0, 0, 0.12)' || imgBorder?.style?.boxShadow === 'rgba(0, 0, 0, 0.12) 1px 1px 10px 0').toBe(true);

    fireEvent.contextMenu(img);

    await findByTestId('bi-contextmenu');

    const optionMenuItem = await findByTestId('menu-item-image-options');

    fireEvent.click(optionMenuItem);
    const sidebar = await findByTestId('image-sidebar');

    expect(sidebar).toBeInstanceOf(Element);

    const restoreBtn = await findByTestId('image-sidebar-restore');
    fireEvent.click(restoreBtn);

    imgBorder = await findByTestId('editor-image-real-box');

    expect(imgBorder?.style?.border).toBe('');
    expect(imgBorder?.style?.borderRadius).toBe('0');
    expect(imgBorder?.style?.boxShadow).toBe('');

    // TODO: 补充推荐样式和对齐方式的 test
  });

  it('图片编辑框&工具栏 - 选中后出现', async () => {
    const { container, findByTestId } = await renderEditor(<Editor defaultValue={complexValue} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    const imageSelectFrame = await findByTestId('image-select-frame');
    const imageInlineToolbar = await findByTestId('inline-toolbar-image');
    expect(imageSelectFrame).toBeInstanceOf(HTMLElement);
    expect(imageInlineToolbar).toBeInstanceOf(HTMLElement);
  });

  it.skip('图片编辑框-缩放后跟随', async () => {
    const { container, findByTestId, findAllByRole } = await renderEditor(<Editor defaultValue={complexValue} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    // 缩放
    const resizer = await findAllByRole('resizer');
    fireEvent.mouseDown(resizer[1]);
    fireEvent.mouseMove(resizer[1], {
      clientX: resizer[1].clientLeft - img.clientWidth / 2,
      clientY: resizer[1].clientTop - img.clientHeight / 2,
    });
    fireEvent.mouseUp(resizer[1]);
    const imageSelectFrame = await findByTestId('image-select-frame');
    expect(imageSelectFrame).toBeInstanceOf(HTMLElement);
  });

  it.skip('图片编辑框&工具栏 - 旋转后跟随', async () => {
    const { container, findByTestId, findAllByRole } = await renderEditor(<Editor defaultValue={value} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);

    // 旋转
    const rotators = await findAllByRole('rotator');
    fireEvent.mouseDown(rotators[1]);
    fireEvent.mouseMove(rotators[1], {
      clientX: rotators[1].clientLeft - img.clientWidth / 2,
      clientY: rotators[1].clientTop + img.clientHeight / 2,
    });
    fireEvent.mouseUp(rotators[1]);
    const imageSelectFrame = await findByTestId('image-select-frame');
    expect(imageSelectFrame).toBeInstanceOf(HTMLElement);
  });

  it('图片编辑框&工具栏 - 调整对齐方式后跟随', async () => {
    const value = Mo.jsonMLToValue([
      'root', {},
      ['p',
        ['img', {
          src: 'https://cn.bing.com/az/hprichbg/rb/TeslaCoil_EN-CN1604235004_1920x1080.jpg',
          width: 192,
          height: 108,
          opacity: 0.5,
          outline: {
            width: 1,
            type: 'solid',
            color: '#002060',
          },
          type: 'jpg',
        }],
      ],
    ]);
    const { container, findByTestId } = await renderEditor(<Editor defaultValue={value} singleLayout={[{ items: [{ name: 'alignment' }] }]}/>);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    // 打开 alignment 工具栏
    const trigger = await findByTestId('bi-toolbar-align');
    fireEvent.click(trigger!);

    // 点击对齐方式
    const alignmentOption = await findByTestId('bi-toolbar-align__list_right');
    fireEvent.click(alignmentOption);
    const imageSelectFrame = await findByTestId('image-select-frame');
    expect(imageSelectFrame).toBeInstanceOf(HTMLElement);
  });

  it('设置图片边框颜色', async () => {
    const { container, queryByTestId, findByTestId, getByTestId } = await renderEditor(<Editor defaultValue={value} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    const inlineToolbar = await findByTestId('inline-toolbar-image');
    const borderPicker = inlineToolbar.querySelector('[data-testid="splitbutton-arrow-BorderPicker"]');
    fireEvent.click(borderPicker);

    const borderPanel = await findByTestId('splite-overlay-BorderPicker');

    const colorBox = borderPanel.querySelector('[color="#C10002"]');
    fireEvent.click(colorBox);

    await waitFor(() => {
      const imgBorder = getByTestId('editor-image-real-box');
      expect(imgBorder.style.borderColor).toBe('#c10002');
    });
  });

  it('行内工具栏 - 更多菜单 - 下载', async () => {
    const { container, findByTestId, getAllByTestId, queryByTestId } = await renderEditor(<Editor defaultValue={value} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    await findByTestId('inline-toolbar-image');
    const moreBtn = getAllByTestId('image-inline-more-dropdown');
    expect(moreBtn.length).toBe(1);
    fireEvent.click(moreBtn[0]);
    const downloadMenuItem = await findByTestId('menu-item-image-menu-download');
    fireEvent.click(downloadMenuItem);
    expect(imageDownloadMockFn).toHaveBeenCalledTimes(1);
  });
  it('行内工具栏 - 更多菜单 - 推荐样式', async () => {
    const { container, findByTestId, queryByTestId, getByTestId } = await renderEditor(<Editor defaultValue={value} />);
    const img = container.querySelector('[data-type="image"]');
    fireEvent.click(img);
    await findByTestId('inline-toolbar-image');
    let recommendStylesButton = queryByTestId('image-toolbar-recommend-styles');
    fireEvent.click(recommendStylesButton);
    await findByTestId('Popup-image-toolbar-recommend-styles');
    const style5 = queryByTestId('image-recommend-style-5');
    const imgBorder = getByTestId('editor-image-real-box');
    fireEvent.click(style5);
    await waitFor(() => {
      expect(imgBorder.style.borderRadius).toBe('0');
    });
    await waitFor(() => {
      expect(imgBorder.style.border).toBe('4px solid #ffffff');
    });
    await waitFor(() => {
      expect(imgBorder.style.boxShadow).toBe('0px 0px 1px rgba(0, 0, 0, 0.24), 0px 2px 10px rgba(0, 0, 0, 0.24)');
    });
    fireEvent.click(img);
    recommendStylesButton = queryByTestId('image-toolbar-recommend-styles');
    fireEvent.click(recommendStylesButton);
    const clearStyle = await findByTestId('image-toolbar-clear-style');
    fireEvent.click(clearStyle);
    await waitFor(() => {
      expect(imgBorder.style.borderRadius).toBe('0');
    });
    await waitFor(() => {
      expect(imgBorder.style.border).toBe('');
    });
    await waitFor(() => {
      expect(imgBorder.style.boxShadow).toBe('');
    });
  });
  it('图片异步转换', async () => {
    const { findByTestId } = await renderEditor(
      <Editor
        defaultValue={value}
        imageCustomConfig={{
          transformImageURL: (url) => {
            return new Promise((resolve) => {
              resolve(`${url}?t=1`);
            });
          },
        }}
      />,
    );
    const img = await findByTestId('cangjie-image');
    await waitFor(() => {
      expect(img.getAttribute('src')).toBe(`${IMAGE_URL}?t=1`);
    });
  });
  it('图片同步转换', async () => {
    const { findByTestId } = await renderEditor(
      <Editor
        defaultValue={value}
        imageCustomConfig={{
          transformImageURL: (url) => `${url}?t=1`,
        }}
      />,
    );
    const img = await findByTestId('cangjie-image');
    await waitFor(() => {
      expect(img.getAttribute('src')).toBe(`${IMAGE_URL}?t=1`);
    });
  });
  it('上传后清理 object url', async () => {
    const revokeObjectURLFn = jest.fn();
    global.URL = class {
      static createObjectURL(file: File) {
        return `blob:/${file.name}`;
      }
      static revokeObjectURL = revokeObjectURLFn;
    };
    global.Image = class {
      onload;
      private _src;
      set src(url: string) {
        this._src = url;
        this.onload && this.onload();
      }
      get src() {
        return this._src;
      }
    };
    const { editor, container } = await renderEditor(
      <Editor
        defaultValue={Mo.textToValue('')}
        singleLayout={[{ items: [{ name: 'image' }] }]}
      />,
    );
    const imageBtn = container.querySelector('[data-testid="bi-toolbar-picture"]');
    fireEvent.click(imageBtn);
    await sleep(100);
    fireEvent.keyDown(editor, getHotkeyEvent(HOTKEYS.deleteBackward));
    fireEvent.keyDown(editor, getHotkeyEvent(HOTKEYS.deleteBackward));
    await sleep(0);
    expect(revokeObjectURLFn).toBeCalledTimes(1);
    global.Image = _Image;
    global.URL = _URL;
  });

  describe('Markdown 编辑', () => {
    const positiveCases = [{
      input: '![]()',
      isPlaceholder: true,
    }, {
      input: '![foo](http://bar)',
      isPlaceholder: false,
    }, {
      input: '![<f o o>](http://bar)',
      isPlaceholder: false,
    }, {
      input: '![](http://bar)',
      isPlaceholder: false,
    }, {
      input: '![](https://www.example.com/foo-1?bar=x#search)',
      isPlaceholder: false,
    }, {
      input: '![](https://www.example.com/foo-1?bar=x,y,z&zoo=a_b_c#search "title 1")',
      isPlaceholder: false,
    }];
    const negativeCases = [
      '![abc]()',
      '![ab]]()',
      '![](())',
      '![](/uri)',
      '![](file:///uri)',
      '![](http://uri"")',
      '![](http://uri"t")',
      '![]("abc")',
      '![](http://uri " abc ")',
      '![](</uri>)',
    ];

    positiveCases.forEach(({ input, isPlaceholder }) => {
      it(`Image: \`${input}\``, async () => {
        const value = ((
          <value>
            <document>
              <block type="paragraph">
                {input}<cursor />
              </block>
            </document>
          </value>
        ) as unknown) as Value;
        const { content, editor, queryByTestId } = await renderEditor(
          <Editor defaultValue={value} />,
        );
        fireEvent.cangjieInput(editor, { data: ' ' });
        await waitForTime(10);

        if (isPlaceholder) {
          const element = await waitFor(() => queryByTestId('image-placeholder'));
          expect(element).toBeTruthy();
        } else {
          const img: HTMLImageElement | null = await waitFor(() => content.querySelector('.image-data-wrapper')) as any;
          expect(img).toBeTruthy();
        }
      });
    });

    negativeCases.forEach((input) => {
      it(`Not Image: \`${input}\``, async () => {
        const value = ((
          <value>
            <document>
              <block type="paragraph">
                {input}<cursor />
              </block>
            </document>
          </value>
        ) as unknown) as Value;
        const { content, editor } = await renderEditor(
          <Editor defaultValue={value} />,
        );
        fireEvent.cangjieInput(editor, { data: ' ' });
        await waitForTime(10);

        expect(content.textContent).toBe(`${input} `);
      });
    })
  });
});
